import React, {useState} from 'react';
import logo from './logo.svg';
import './App.css';
import {Navbar, Nav, Jumbotron, Button} from 'react-bootstrap';
import Data from './data';
import Detail from './Detail';
import Cart from './Cart';
import axios from 'axios';
import {Link, Route, Switch, useHistory} from 'react-router-dom';

function App() {

  let [beer, setBeer] = useState(Data);
  let [page, setPage] = useState(2);

  return (
    <div className="App">
      <Navbar bg="light" variant="light">
      <Navbar.Brand href="#home">Beer Shop</Navbar.Brand>
      <Nav className="mr-auto">
        <Nav.Link as={Link} to ="/">Home</Nav.Link>
        <Nav.Link as={Link} to ="#">Cart</Nav.Link>
      </Nav>
    </Navbar>

    <Switch>
      <Route exact path="/">
        <Jumbotron className="background">
          <h1>Holiday Sale 20% Off</h1>
          <p>
            Shop for the premier beer brands around the world :)
          </p>
          <p>
            <Button variant="primary">Learn more</Button>
          </p>
        </Jumbotron>
        <div className="container">
          <div className="row">
              {
                beer.map((a,i) => {
                  return (
                    <Card beer = {a} key = {i}></Card>
                  )
                })
              }
          </div>
          <button className="btn btn-primary" onClick={()=> {
            axios.get("https://api.punkapi.com/v2/beers?page=" + page + "&per_page=3")
            .then((result) => {
              let copyBeer = [...beer, ...result.data];
              let copyPage = page;
              setBeer(copyBeer);
              setPage(copyPage + 1);
            })
            .catch(() => {console.log("Failure")})
          }}>More</button>
        </div>
      </Route>

      <Route path="/detail/:id">
        <Detail beer = {beer} />
      </Route>

      <Route path="/cart">
        <Cart />
      </Route>
    </Switch>
    </div>
  );
}

function Card(props) {
  let history = useHistory();
  return(
    <div className="col-md-4" onClick={() => { history.push('/detail/' + props.beer.id)}}>
      <img src={props.beer.image_url} width="auto" height="200px" alt="img" />
      <h4>{props.beer.name}</h4>
      <p>{props.beer.tagline}</p>
    </div>
  )
}

export default App;
