import React, { useEffect, useState } from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss'
import { connect } from 'react-redux';

let Box = styled.div`
    padding: 20px;
`;

let Title = styled.h4`
    font-size: 25px;
`;

export function Detail(props) {
    let {id} = useParams();
    let history = useHistory();
    let item = props.beer.find((a) => {return (a.id == id)});
    let [alert, setAlert] = useState(true);

    useEffect(() => {
      let timer = setTimeout(() => {setAlert(false)}, 2000);
      return () => {clearTimeout(timer);}
    },[alert]);
    
    return(
      <div className="container">
          <Box><Title>Detail</Title></Box>

          {
            alert === true 
            ? <div className="my-alert-yellow">
                <p>Almost sold out</p>
              </div>
            : null
          }

          <div className="row">
            <div className="col-md-6">
              <img src={item.image_url} width="auto" height="500px" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{item.name}</h4>
              <p>{item.tagline}</p>
              <p>{item.description}</p>
              <button className="btn btn-danger" onClick={() => {
                props.dispatch({type: "order", payload: {id: item.id, name: item.name, qt: 1}});
                //console.log({id: item.id, name: item.name, qt: 1});
                //onsole.log(props.state)
                history.push('/cart');
              }}>Order</button> 
              &nbsp;
              <button className="btn btn-danger" onClick={() => {history.push('/')}}>Back</button>
            </div>
          </div>
        </div> 
    )
  }

function mapStateToProps(state) {
  return {
      state: state
  }
}

export default connect(mapStateToProps)(Detail);