import React from 'react';
import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';

function Cart(props) {
    let history = useHistory();
    return(
        <div>
            <Table responsive>
            <thead>
                <tr>
                <th>Id</th>
                <th>Product</th>
                <th>Quantity</th>
                <th>Edit</th>
                </tr>
            </thead>
            
            <tbody>
                
                {
                    props.state.map((a,i)=>{
                        return (
                            <tr key = {i}>
                                <td>{a.id}</td>
                                <td>{a.name}</td>
                                <td>{a.qt}</td>
                                <td>
                                    <button onClick={() => {
                                        props.dispatch({type: "increase", data: a.id});
                                    }}>+</button>
                                    <button onClick={() => {
                                        props.dispatch({type: "decrease", data: a.id});
                                    }}>-</button>
                                </td>       
                            </tr>
                        )
                    })
                }
            </tbody>
            </Table>

            <button onClick={() => {history.goBack();}}>Back</button>
        </div>
    )
}

function mapStateToProps(state) {
    return {
        state: state
    }
}

export default connect(mapStateToProps)(Cart);

